/**
 * RoomTimeline model events
 */

'use strict';

import {EventEmitter} from 'events';
import RoomTimeline from './../../models/roomTimeline.model.js';
var RoomTimelineEvents = new EventEmitter();

// Set max event listeners (0 == unlimited)
RoomTimelineEvents.setMaxListeners(0);

// Model events
var events = {
  'save': 'save',
  'remove': 'remove'
};

// Register the event emitter to the model events
for (var e in events) {
  var event = events[e];
  RoomTimeline.schema.post(e, emitEvent(event));
}

function emitEvent(event) {
  return function(doc) {
    RoomTimelineEvents.emit(event + ':' + doc._id, doc);
    RoomTimelineEvents.emit(event, doc);
  }
}

export default RoomTimelineEvents;
